/* Window Component Styles - Mac OS 9 */

/* Import pixelated corners utility */
@import '../../styles/pixelated-corners.css';

.window {
	/* Base window styling */
	display: flex;
	flex-direction: column;
	
	/* Mac OS 9 window appearance */
	background-color: var(--color-gray-300); /* #DDDDDD - authentic window background */
	border: var(--border-width-thin) solid var(--color-black);
	
	/* Authentic Mac OS 9 drop shadow: sharp 2px offset, no blur */
	filter: drop-shadow(2px 2px 0px rgba(0, 0, 0, 0.5));
	
	/* Layout */
	position: relative;
	overflow: hidden;
	
	/* Default dimensions */
	width: auto;
	height: auto;
	min-width: 200px;
	min-height: 100px;
	
	/* Pixelated corners for authentic Mac OS 9 appearance */
	clip-path: polygon(
		/* Top-left stepped corner */
		4px 0,
		3px 1px,
		2px 2px,
		1px 3px,
		0 4px,
		/* Left edge */
		0 calc(100% - 4px),
		/* Bottom-left stepped corner */
		1px calc(100% - 3px),
		2px calc(100% - 2px),
		3px calc(100% - 1px),
		4px 100%,
		/* Bottom edge */
		calc(100% - 4px) 100%,
		/* Bottom-right stepped corner */
		calc(100% - 3px) calc(100% - 1px),
		calc(100% - 2px) calc(100% - 2px),
		calc(100% - 1px) calc(100% - 3px),
		100% calc(100% - 4px),
		/* Right edge */
		100% 4px,
		/* Top-right stepped corner */
		calc(100% - 1px) 3px,
		calc(100% - 2px) 2px,
		calc(100% - 3px) 1px,
		calc(100% - 4px) 0
	);
}

/* ========================================
 * Window States
 * ======================================== */

.window--active {
	/* Active window - standard appearance */
	z-index: 10;
}

.window--inactive {
	/* Inactive window - slightly dimmed */
	opacity: 0.92;
	z-index: 1;
}

/* ========================================
 * Title Bar
 * ======================================== */

.titleBar {
	/* Title bar container */
	display: flex;
	align-items: center;
	gap: var(--spacing-2);
	
	/* Styling - authentic Mac OS 9 header color */
	background-color: #CBCBCB;
	border-bottom: var(--border-width-thin) solid var(--color-black);
	
	/* Padding */
	padding: var(--spacing-1_5) var(--spacing-2);
	
	/* Height */
	min-height: 22px;
	
	/* Prevent selection */
	user-select: none;
	
	/* Position for pseudo-elements */
	position: relative;
}

/* Header striped lines - left side */
.titleBar::before {
	content: '';
	position: absolute;
	left: 4px;
	top: 50%;
	transform: translateY(-50%);
	width: 80px;
	height: 12px;
	
	/* 6 horizontal lines pattern */
	background: repeating-linear-gradient(
		to bottom,
		transparent 0px,
		transparent 1px,
		var(--color-gray-500) 1px,
		var(--color-gray-500) 2px,
		transparent 2px,
		transparent 3px
	);
	
	/* Prevent interaction */
	pointer-events: none;
}

/* Header striped lines - right side */
.titleBar::after {
	content: '';
	position: absolute;
	right: 4px;
	top: 50%;
	transform: translateY(-50%);
	width: 80px;
	height: 12px;
	
	/* 6 horizontal lines pattern */
	background: repeating-linear-gradient(
		to bottom,
		transparent 0px,
		transparent 1px,
		var(--color-gray-500) 1px,
		var(--color-gray-500) 2px,
		transparent 2px,
		transparent 3px
	);
	
	/* Prevent interaction */
	pointer-events: none;
}

/* ========================================
 * Window Controls
 * ======================================== */

.controls {
	/* Control buttons container */
	display: flex;
	gap: var(--spacing-1);
	align-items: center;
}

.controlButton {
	/* Base control button */
	width: 12px;
	height: 12px;
	padding: 0;
	margin: 0;
	
	/* Remove button defaults */
	background: transparent;
	border: var(--border-width-thin) solid var(--color-border);
	cursor: pointer;
	
	/* Box shadow for depth */
	box-shadow: var(--shadow-inset);
	
	/* Transitions */
	transition: none;
}

.controlButton:hover {
	background-color: var(--color-gray-300);
}

.controlButton:active {
	box-shadow: var(--shadow-pressed);
}

.controlButton:focus-visible {
	outline: 1px dotted var(--color-focus);
	outline-offset: 1px;
}

/* Control button visual indicators */
.closeBox,
.minimizeBox,
.maximizeBox {
	width: 100%;
	height: 100%;
	position: relative;
}

/* Close button: X pattern */
.closeBox::before,
.closeBox::after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	width: 6px;
	height: 1px;
	background-color: var(--color-border);
}

.closeBox::before {
	transform: translate(-50%, -50%) rotate(45deg);
}

.closeBox::after {
	transform: translate(-50%, -50%) rotate(-45deg);
}

/* Minimize button: dash */
.minimizeBox::before {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 6px;
	height: 1px;
	background-color: var(--color-border);
}

/* Maximize button: small square */
.maximizeBox::before {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 4px;
	height: 4px;
	border: 1px solid var(--color-border);
}

/* ========================================
 * Title Text
 * ======================================== */

.titleText {
	/* Title text styling */
	font-family: var(--font-system);
	font-size: var(--font-size-md);
	font-weight: var(--font-weight-bold);
	line-height: var(--line-height-snug);
	color: var(--color-text);
	
	/* Center alignment */
	flex: 1;
	text-align: center;
	
	/* Truncate long titles */
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

/* ========================================
 * Content Area
 * ======================================== */

.content {
	/* Content area */
	flex: 1;
	overflow: auto;
	
	/* Background - white content area like authentic Mac OS 9 */
	background-color: var(--color-gray-100);
	
	/* Padding */
	padding: var(--spacing-4);
	
	/* Position for scrolling */
	position: relative;
	
	/* Authentic inset border */
	margin: var(--spacing-2);
	border: var(--border-width-thin) solid #555555;
}

/* ========================================
 * Resize Handle
 * ======================================== */

.resizeHandle {
	/* Resize handle in bottom-right corner */
	position: absolute;
	bottom: 0;
	right: 0;
	width: 12px;
	height: 12px;
	
	/* Visual indicator (diagonal lines) */
	background-image: linear-gradient(
		135deg,
		transparent 0%,
		transparent 40%,
		var(--color-border) 40%,
		var(--color-border) 45%,
		transparent 45%,
		transparent 55%,
		var(--color-border) 55%,
		var(--color-border) 60%,
		transparent 60%
	);
	
	/* Cursor */
	cursor: nwse-resize;
	
	/* Layering */
	z-index: 5;
}

/* ========================================
 * Accessibility
 * ======================================== */

/* High contrast mode */
@media (prefers-contrast: high) {
	.window {
		border-width: 2px;
	}
	
	.controlButton {
		border-width: 2px;
	}
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
	.controlButton {
		transition: none;
	}
}
